.top-wrap {
  display: flex;
  width: 100%;
  height: 70px;
  flex-wrap: nowrap;
}

.top-wrap form {
  flex-grow: 1;
}

.top-wrap .searchInput {
  width: calc(100% - 70px);
  min-width: 200px;
  height: 100%;
  outline: none;
  border: 0;
  background: rgb(246, 249, 249);
  box-sizing: border-box;
  padding: 1.5rem;
  font-size: 1.2rem;
  font-weight: 300;
}

.top-wrap .searchInput:hover {
  outline: none;
  border: 0;
}

.top-wrap .searchBtn {
  width: 70px;
  height: 100%;
  outline: none;
  background: rgb(47, 215, 159);
  color: white;
  border: 0;
  font-size: 1.2rem;
  cursor: pointer;
}